.sidebar {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    overflow: hidden;
    z-index: 2000;
}
.sidebar.collapsed {
    width: 40px;
}
@media (min-width: 768px) {
    .sidebar {
        top: 10px;
        bottom: 10px;
        transition: width 500ms;
    }
}
@media (min-width: 768px) and (max-width: 991px) {
    .sidebar {
        width: 305px;
    }
}
@media (min-width: 992px) and (max-width: 1199px) {
    .sidebar {
        width: 390px;
    }
}
@media (min-width: 1200px) {
    .sidebar {
        width: 460px;
    }
}

.sidebar-left {
    left: 0;
}
@media (min-width: 768px) {
    .sidebar-left {
        left: 10px;
    }
}

.sidebar-right {
    right: 0;
}
@media (min-width: 768px) {
    .sidebar-right {
        right: 10px;
    }
}

.sidebar-tabs {
    top: 0;
    bottom: 0;
    height: 100%;
    background-color: #fff;
}
.sidebar-left .sidebar-tabs {
    left: 0;
}
.sidebar-right .sidebar-tabs {
    right: 0;
}
.sidebar-tabs,
.sidebar-tabs > ul {
    position: absolute;
    width: 40px;
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.sidebar-tabs > li,
.sidebar-tabs > ul > li {
    width: 100%;
    height: 40px;
    color: #333;
    font-size: 12pt;
    overflow: hidden;
    transition: all 80ms;
}
.sidebar-tabs > li:hover,
.sidebar-tabs > ul > li:hover {
    color: #000;
    background-color: #eee;
}
.sidebar-tabs > li.active,
.sidebar-tabs > ul > li.active {
    color: #fff;
    background-color: #3f51b5;
}
.sidebar-tabs > li.disabled,
.sidebar-tabs > ul > li.disabled {
    color: rgba(51, 51, 51, 0.4);
}
.sidebar-tabs > li.disabled:hover,
.sidebar-tabs > ul > li.disabled:hover {
    background: transparent;
}
.sidebar-tabs > li.disabled > a,
.sidebar-tabs > ul > li.disabled > a {
    cursor: default;
}
.sidebar-tabs > li > a,
.sidebar-tabs > ul > li > a {
    display: block;
    width: 100%;
    height: 100%;
    line-height: 40px;
    color: inherit;
    text-decoration: none;
    text-align: center;
}
.sidebar-tabs > ul + ul {
    bottom: 0;
}

.sidebar-content {
    position: absolute;
    top: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.95);
    /*overflow-x: hidden;*/
    /*overflow-y: auto;*/
}
.sidebar-left .sidebar-content {
    left: 40px;
    right: 0;
}
.sidebar-right .sidebar-content {
    left: 0;
    right: 40px;
}
.sidebar.collapsed > .sidebar-content {
    overflow-y: hidden;
}

.sidebar-pane {
    display: none;
    left: 0;
    right: 0;
    box-sizing: border-box;
    padding: 10px 20px;
}
.sidebar-pane.active {
    display: block;
}
@media (min-width: 768px) and (max-width: 991px) {
    .sidebar-pane {
        min-width: 265px;
    }
}
@media (min-width: 992px) and (max-width: 1199px) {
    .sidebar-pane {
        min-width: 350px;
    }
}
@media (min-width: 1200px) {
    .sidebar-pane {
        min-width: 420px;
    }
}

