    .sidebar-left ~ .sidebar-map {
        margin-left: 0;
    }
}

.sidebar-right ~ .sidebar-map {
    margin-right: 40px;
}
@media (min-width: 768px) {
    .sidebar-right ~ .sidebar-map {
